*{
  maargin:0;
  padding: 0;
  box-sizing: border-box;
}
.navbar{
  background-color: #fff;
}
.navbar-brand{
  font-size: 1.9rem!important;
  color: #565387!important;
}
.navbar a{
  font-size: 1.3rem;
  text-transform: capitalize;
}
.navbar a:hover{
  color: #3498db;
}
.menu_active{
  font-weight: bold;
  border-bottom: 1px solid #565387;
}
#header{
  width: 100%;
  height: 80vh;
margin-top: 70px;
}
#header .brand-name{
  color: #3498db;
}
#header h1{
  margin: 0 0 10px 0;
  font-size: 48px;
  font-weight: 700;
  color: black;
}
#header h2{
  color: #484848;
  font-size: 24px;
}
#header .btn-get-started{
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  border: 2px solid #3498db;
  text-decoration: none;
  color: #3498db;
  transition: 0.5s; 
}
#header .btn-get-started:hover{
  background-color: #3498db;
  color: #fff;
}
.header-img{
  text-align: right;
}
#header .animated{
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@keyframes up-down{
  0%{
    transform: translateY(20px);
  }
  100%{
    transform: translateY(-10px);
  }
}
.card img{
  width:320px;
  height:400px;
}